@font-face {
  font-family: "Inter";
  src: url("Inter-SemiBold.woff2") format("woff2"), url("Inter-SemiBold.woff") format("woff");
  font-weight: 600;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: url("Inter-Medium.woff2") format("woff2"), url("Inter-Medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}
@font-face {
  font-family: "Inter";
  src: url("Inter-Regular.woff2") format("woff2"), url("Inter-Regular.woff") format("woff");
  font-weight: normal;
  font-style: normal;
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Inter", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  width: 100%;
  height: 100%;
  background-color: #ffffff;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.hidden {
  display: none;
}

.rouble {
  font-family: "Inter", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1440px;
  margin: 0 auto;
  padding: 0 32px;
}
@media (max-width: 767px) {
  .container {
    padding: 0 16px;
  }
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #D22020;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #D22020;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.page-header {
  z-index: 3;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: #eaeaea;
}

.page-header__wrapper {
  display: flex;
  justify-content: space-between;
}

.page-header__logo {
  fill: #D22020;
  margin-top: 24px;
}

.page-header__button {
  display: none;
}

.page-header__list {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  margin-top: 24px;
}

.page-header__item {
  margin-left: 32px;
  font-family: "Inter", "Arial", sans-serif;
  font-weight: 400;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.0357142857em;
  display: flex;
  align-items: center;
  min-height: 44px;
}
.page-header__item--button {
  background-color: #D22020;
  border-radius: 30px;
  padding: 0 20px;
}
.page-header__item--button .page-header__link {
  color: #ffffff;
}

@media (hover: hover) {
  .page-header__link:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
.page-header__link.focus-visible:focus {
  opacity: 0.5;
}

@media (max-width: 767px) {
  .page-header--close {
    min-height: 62px;
    position: relative;
  }
  .page-header--open {
    position: fixed;
    z-index: 3;
    top: 0;
    right: 0;
    height: 100%;
    width: 100%;
    background-color: #ffffff;
  }
  .page-header__wrapper--close {
    padding-left: 15px;
    padding-right: 15px;
    display: block;
  }
  .page-header__wrapper--open {
    height: 100%;
  }
  .page-header__link-logo {
    display: block;
    width: 100px;
    height: 45px;
    margin-top: 24px;
  }
  .page-header__content-header {
    display: flex;
    justify-content: space-between;
  }
  .page-header__logo {
    margin-top: 0;
  }
  .page-header__list--close {
    display: none;
  }
  .page-header__list--open {
    overflow-y: scroll;
    display: block;
    width: 100%;
    height: calc(100% - 74px);
    margin-top: 24px;
    padding-left: 0;
  }
  .page-header__button {
    display: block;
    border: none;
    background-repeat: no-repeat;
    padding: 0;
  }
  .page-header__button--close {
    background: url("../img/svg/open.svg"), rgba(255, 255, 255, 0);
    width: 24px;
    height: 16px;
    margin-top: 39px;
    margin-right: 5px;
  }
  .page-header__button--open {
    background: url("../img/svg/close.svg"), rgba(255, 255, 255, 0);
    width: 21px;
    height: 21px;
    margin-top: 36px;
    margin-right: 7px;
  }
  .page-header__item--open {
    margin: 0;
    width: 100%;
    height: 40px;
  }
  .page-header__item--button {
    display: none;
  }
}
.promo {
  padding-top: 65.3%;
  position: relative;
}

.promo__wrapper {
  position: absolute;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 100%;
  top: 0;
  padding-bottom: 30px;
}

.promo__wrapper-social {
  display: flex;
  justify-content: end;
  position: relative;
}

.promo__social-list {
  display: flex;
  flex-wrap: wrap;
  list-style-type: none;
  margin: 0;
  padding: 0;
  max-width: 48%;
}

.promo__social-item {
  margin-left: 36px;
}

.promo__social-link {
  font-family: "Inter", "Arial", sans-serif;
  font-size: 18px;
  line-height: 150%;
  letter-spacing: 0.005em;
  text-transform: uppercase;
}
@media (hover: hover) {
  .promo__social-link:hover:not(.focus-visible) {
    opacity: 0.5;
  }
}
.promo__social-link.focus-visible:focus {
  opacity: 0.5;
}

.promo__social-name {
  margin-left: 3px;
}

.promo__title {
  font-family: "Inter", "Arial", sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 150%;
  letter-spacing: 0.0434782609em;
  margin-top: 110px;
  margin-bottom: 0;
}

.promo__description {
  display: block;
  width: 213px;
  font-family: "Inter", "Arial", sans-serif;
  font-size: 16px;
  line-height: 150%;
  letter-spacing: 0.0333333333em;
}

.promo__icon {
  position: absolute;
  bottom: 16px;
  left: calc(50% + 11px);
}

@media (min-width: 1440px) {
  .promo {
    padding-top: 0;
    min-height: 940px;
  }
  .promo__wrapper {
    position: relative;
    min-height: 940px;
  }
}
@media (max-width: 767px) {
  .promo {
    padding-top: 0;
    min-height: 499px;
  }
  .promo__title {
    margin-top: 41px;
  }
  .promo__icon {
    bottom: 28px;
    left: calc(50% - 8px);
  }
  .promo__social-name {
    display: none;
  }
  .promo__social-icon--whats-app {
    width: 40px;
    height: 48px;
  }
  .promo__social-icon--telegram {
    margin-top: 8px;
    width: 40px;
    height: 34px;
  }
  .promo__social-link {
    display: block;
  }
  .promo__social-item {
    margin-left: 15px;
  }
  .promo__wrapper {
    padding-bottom: 25px;
  }
  .promo__social-list {
    margin-right: 4px;
  }
}
.background-wrapper {
  background-image: url("../img/promo.jpg");
  background-position: center top;
  background-repeat: no-repeat;
  background-color: #eaeaea;
}

@media (min-resolution: 144dpi), (min-resolution: 1.5dppx) {
  .background-wrapper {
    background-image: url("../img/promo@2x.jpg");
  }
}
@media (min-width: 1440px) {
  .background-wrapper {
    background-size: 1440px 1024px;
  }
}
@media (max-width: 1439px) {
  .background-wrapper {
