  font-family: "Inter", "Arial", sans-serif;
  font-weight: 500;
  font-size: 24px;
  line-height: 130%;
  letter-spacing: 0.0096153846em;
  margin-top: 12px;
  margin-bottom: 0;
  color: #212529;
