.portfolios__item-description {
  display: block;
  color: #6C757D;
  font-family: "Inter", "Arial", sans-serif;
  font-size: 18px;
  line-height: 150%;
  margin-top: 2px;
  letter-spacing: 0.0333333333em;
  z-index: 2;
  width: fit-content;
}
@media (hover: hover) {
  .portfolios__item-description:hover:not(.focus-visible) {
    color: #212529;
