  font-size: 18px;
  line-height: 30px;
  text-transform: uppercase;
  border: 1px solid #D22020;
  border-radius: 36px;
  background-color: rgba(255, 255, 255, 0);
  color: #D22020;
  margin-left: auto;
  margin-right: auto;
